import React from 'react';
import { graphql, Link } from 'gatsby';

import Layout from '../components/common/layout';
import HomeCarousel from '../components/home/HomeCarousel';
import VimeoEl from '../components/common/Vimeo';
import CreateTiles from '../components/common/createTiles';

import homeStyles from '../styles/home.module.scss';

export default ({ data }) => {
  const entry = data.craft.entries[0];

  return (
    <Layout className={homeStyles.container} seo={entry.seomatic}>
      <HomeCarousel data={[...entry.homeBanner]} styles={homeStyles} />

      <div className="row">
        <div className="col__2-5 col__m-1-5 col__s-0-5" />
        <div className="col__7 col__m-8-5 col__s-11">
          <div className={homeStyles.heading}>
            <h1 className={`${homeStyles.heading_text} fontSize-h2`}>{entry.homeIntroText}</h1>
          </div>
        </div>
        <div className="col__2-5 col__m-1-5 col__s-0-5" />
      </div>

      <div className="row article-media">
        <VimeoEl
          className="vimeo__video aspect__16-by-9"
          vimeoOptions={{ id: entry.homeVimeo.split('/').pop(), loop: true }}
        />
      </div>

      <div className="row">
        <div className="col__6 col__m-4 col__s-3" />
        <div className="col__6 col__m-8 col__s-9">
          <div className={homeStyles.subheading}>
            <h2 className={`${homeStyles.subheading_text} fontSize-h3`}>{entry.homeSubText}</h2>
          </div>
        </div>
      </div>

      <div className="row">
        <CreateTiles data={entry.projectsFeed} />
      </div>

      <div className="row">
        <div className="col__6 col__m-4 col__s-3" />
        <div className="col__6 col__m-8 col__s-9">
          <div className={homeStyles.subheading}>
            <h2 className={`${homeStyles.subheading_text} fontSize-h3`}>
              We believe in total design. Our way of joining the dots and making design work harder.
              With a seamless connection of different design disciplines, we can bring identity to
              life in all ways. In motion, through spaces, in places, on the line, on the street, in
              the hand, in the&nbsp;moment.
            </h2>

            <Link className={homeStyles.subheading_link} to="/work">
              <p className={`${homeStyles.subheading_link_text} fontSize-p2`}>
                See more of the work
              </p>
            </Link>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export const query = graphql`
  query {
    craft {
      entries(section: home) {
        ... on Craft_Home {
          seomatic {
            metaTagContainer
            metaLinkContainer
            metaTitleContainer
            metaScriptContainer
            metaJsonLdContainer
          }
          homeBanner {
            ... on Craft_HomeBannerHomeBannerSlide {
              slideTagline
              slideClientName
              slideBannerImage {
                id
                dateUpdated
                url

                ... on Craft_UploadsVolume {
                  __typename
                  imageFile {
                    childImageSharp {
                      fluid(maxWidth: 1920) {
                        src
                        sizes
                        srcSet
                        srcWebp
                        srcSetWebp
                        aspectRatio
                      }
                    }

                    colors {
                      muted
                    }
                  }
                }
              }
              slideVideo {
                url
              }
              slideAmbientBackgroundVideo
              slideLinkToStory {
                slug
              }
            }
          }
          homeVimeo
          homeIntroText
          homeSubText
          projectsFeed(hidden: false) {
            ... on Craft_Projects {
              previewTile {
                id
                dateUpdated
                url

                ... on Craft_UploadsVolume {
                  __typename
                  imageFile {
                    childImageSharp {
                      fluid(maxWidth: 650) {
                        src
                        sizes
                        srcSet
                        srcWebp
                        srcSetWebp
                        aspectRatio
                      }
                    }

                    colors {
                      muted
                    }
                  }
                }
              }
              title
              slug
              description {
                content
              }
            }
          }
        }
      }
    }
  }
`;
