import React, { useState, useEffect, createRef, useRef, useCallback, useMemo } from 'react';
import AniLink from 'gatsby-plugin-transition-link/AniLink';
import { TweenLite, Sine } from 'gsap';

import Img from 'gatsby-image';
import fluidImgProps from '../common/fluidImgProps';
import Vimeo from '../common/Vimeo';
import { urlToId } from '../article/vimeo';
import dragEvents from './carouselDragEvents';
import CursorOverlay from '../common/cursorOverlay';

const pageChangeTransition = 0.3;

export default ({ data }) => {
  const [active, setActive] = useState(0);
  const activeRef = useRef(active);
  const carousel = useRef();
  const slideContainer = createRef();

  const addDragEvents = useCallback((el, slides) => {
    dragEvents('ADD', el, slides, activeRef, setActive);
  }, []);

  useEffect(() => {
    addDragEvents(carousel.current, slideContainer.current);
    const carouselRef = carousel.current;
    return () => dragEvents('REMOVE', carouselRef);
  }, [addDragEvents, slideContainer]);

  useEffect(() => {
    activeRef.current = active;
    const translateX = carousel.current.offsetWidth * active * -1;
    TweenLite.to(slideContainer.current, 0.4, { x: translateX + 'px', ease: Sine.easeOut });
  }, [active, slideContainer]);

  return (
    <div
      ref={carousel}
      className="home-carousel home-carousel-twelve-by-five home-carousel__fullWidth -focused"
    >
      <div className="home-carousel-dragging" />

      <div className="home-carousel__mobileDelayLink">
        {data.map((slide, i) => (
          <AniLink
            className={`home-carousel__mobileDelayLink-link ${i === active ? '-active' : ''}`}
            key={'carousel-link-' + i}
            to={'/work/articles/' + slide.slideLinkToStory[0].slug}
            duration={pageChangeTransition}
            fade
            draggable={false}
          />
        ))}
      </div>

      <div
        ref={slideContainer}
        className="home-carousel__container"
        style={{ width: `${100 * data.length}%` }}
      >
        {data.map((slide, i) => {
          const imageProps = useMemo(() => fluidImgProps(slide.slideBannerImage[0]), [
            slide.slideBannerImage,
          ]);

          return (
            <section className={`home-carousel__slide -active`} key={'carousel-media-' + i}>
              <Img
                {...imageProps}
                className="home-carousel__slide-background"
                key={'carousel-slide-' + i}
                style={{
                  position: 'absolute',
                  width: '100%',
                  height: '100%',
                }}
              />

              {slide.slideAmbientBackgroundVideo ? (
                <Vimeo
                  className="vimeo__video home-carousel__slide-vimeo"
                  vimeoOptions={{
                    id: urlToId(slide.slideAmbientBackgroundVideo),
                    loop: true,
                    background: true,
                  }}
                  videoPlayingCallback={el => el.classList.add('-active')}
                />
              ) : null}
            </section>
          );
        })}
      </div>

      <div className="home-carousel__overlay">
        <div className="home-carousel__overlay-items">
          {data.map((slide, i) => (
            <AniLink
              className={`home-carousel__text fontSize-h1 ${i === active ? '-active' : ''}`}
              key={'carousel-text-' + i}
              to={'/work/articles/' + slide.slideLinkToStory[0].slug}
              duration={pageChangeTransition}
              fade
            >
              <p className="home-carousel__text-client fontSize-p3 fontSize-uppercase">
                {slide.slideClientName}
              </p>
              <h3 className="home-carousel__text-title fontSize-h1">{slide.slideTagline}</h3>
            </AniLink>
          ))}

          <div className="home-carousel__pagination fontSize-h1">
            {data.map((slide, i) => (
              <div
                className={`home-carousel__dot ${i === active ? '-active' : ''}`}
                key={'slide-' + i}
                onClick={() => setActive(i)}
              />
            ))}
          </div>
        </div>
      </div>

      <div className="home-carousel__sides">
        <CursorOverlay clickable={false} childState={active}>
          <div
            className="home-carousel__left"
            style={{ display: active !== 0 ? 'block' : 'none' }}
            onClick={() => setActive(active - 1)}
          />
        </CursorOverlay>

        <CursorOverlay clickable={false} childState={active}>
          <div
            className="home-carousel__right"
            style={{ display: active !== data.length - 1 ? 'block' : 'none' }}
            onClick={() => setActive(active + 1)}
          />
        </CursorOverlay>
      </div>

      <div className="home-carousel__gradientBackground" />
    </div>
  );
};
