const defaultBackgroundColor = '#efefef';

export default imgData => {
  const { imageFile, url } = imgData;
  const imgSharp = imageFile && imageFile.childImageSharp ? imageFile.childImageSharp : null;
  const fluid =
    imgSharp !== null ? imgSharp.fluid : { src: url, sizes: '', srcSet: url, aspectRatio: 1.41 };
  const backgroundColor =
    imageFile && imageFile.colors !== null ? imageFile.colors.muted : defaultBackgroundColor;

  return { fluid, backgroundColor };
};
