import { TweenLite, Sine } from 'gsap';

const draggingThreshold = 10; // px
const nextSlideThreshold = 0.1;

let isDown = false;
let isDragging = false;
let slidePosition = 0;
let startX = 0;
let moveX = 0;
let slideCount;
let down, move, end;
let events;

export default (action, el, slides, activeRef, setActive) => {
  if (action === 'ADD') {
    slideCount = slides.children.length;

    down = e => onStart(e, el, activeRef);
    move = e => onMove(e, el, slides);
    end = e => onEnd(e, el, slides, activeRef, setActive);

    events = [
      ['mousedown', down],
      ['mousemove', move],
      ['mouseup', end],
      ['mouseleave', end],

      ['touchstart', down],
      ['touchmove', move],
      ['touchend', end],
      ['touchcancel', end],
    ];

    for (const event of events) {
      el.addEventListener(event[0], event[1]);
    }
  }

  if (action === 'REMOVE') {
    for (const event of events) {
      el.removeEventListener(event[0], event[1]);
    }
  }
};

const onStart = (e, el, activeRef) => {
  const x = e.pageX || e.touches[0].pageX;

  slidePosition = activeRef.current * el.offsetWidth;
  startX = slidePosition + x;
  isDown = true;
};

const onMove = (e, el, slides) => {
  const x = e.pageX || e.touches[0].pageX;

  if (isDown) {
    if (!isDragging) {
      if (x + draggingThreshold < startX || x - draggingThreshold > startX) {
        isDragging = true;
        el.classList.add('-dragging'); // to prevent unintentional clicks on links
      }
    } else {
      e.preventDefault();
      moveX = x - startX;
      TweenLite.to(slides, 0.1, { x: moveX + 'px', ease: Sine.easeOut });
    }
  }
};

const onEnd = (e, el, slides, activeRef, setActive) => {
  if (isDragging) {
    const { current } = activeRef;
    const threshold = nextSlideThreshold * el.offsetWidth;

    if (current !== slideCount - 1 && moveX + threshold < slidePosition * -1) {
      setActive(current + 1);
    } else if (current !== 0 && moveX - threshold > slidePosition * -1) {
      setActive(current - 1);
    } else {
      TweenLite.to(slides, 0.4, { x: slidePosition * -1 + 'px', ease: Sine.easeOut });
    }
  }

  isDown = false;
  isDragging = false;
  el.classList.remove('-dragging');
};
